import Sorting from "../layout-settings/sorting";
import Roles from "../layout-settings/roles";
import Users from "../layout-settings/users";

export default class LayoutSettings {

	constructor( el ) {
		this.element = el;
		this.settings = new Map();
		this.init();
	}

	init() {
		this.settings.set( 'sorting', new Sorting( this.element ) );
		this.settings.set( 'roles', new Roles( this.element ) );
		this.settings.set( 'users', new Users( this.element ) );
	}

}